body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #c3c7cc;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  justify-content: center;
  align-items: center;
  width: 70%;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}


.button {
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
}

.buttonField {
    height: 70px;
}

.iframeField {
    margin-top: 50px;
    margin-right: 10px;
    margin-left: 10px;
    min-width: 100vw;
}

html, body {
    margin: 0;
    padding: 0;
    min-height: 100%;
    width: 100%;
    background-color: #c3c7cc;
}

div {
    height: 100%;
    width: 100%;
}
